import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { PluginManagerService, componentData } from 'plugin-manager';

@Component({
  selector: 'llib-carousel-card',
  templateUrl: './carousel-card.component.html',
  styleUrls: ['./carousel-card.component.styl']
})
export class CarouselCardComponent implements OnInit {

  data: any;
  color: any;
  pageData: Array<any>;
  pageIndex: number = 0;
  scrollLeft: number = 0;

  @ViewChild('scrollContainer')
  scrollContainerDiv: ElementRef


  constructor(private pluginManagerService: PluginManagerService, comData: componentData) {
    // æ¥æ¶ä¸­é´ä»¶ä¼ å¼
    console.log("comData", comData)
    this.data = comData.configData.data
    this.color = comData.configData.color
  }

  ngOnInit() {
    if (!this.data.autoplay) {
      this.initPageConfig()
    }
  }

  initPageConfig() {
    console.log("æ§è¡initPageConfig")
    let num = 0 //æ»å±å¤å°å smallå 1 mediumå 2
    let columns = this.data.columns //æ¯è¡å¤å°ä¸ª
    let onePageCardNum = 4 * columns //æ¯é¡µæå¤å¤å°ä¸ª

    let pageData = []

    let currentPage = []; // å­å¨å½åé¡µçæ°æ®
    let remainingBlocks = onePageCardNum; // å½åé¡µå©ä½å¯å®¹çº³åæ°

    for (const item of this.data.list) {
      let blockCount = 0; // å½åitemå æ®çåæ°
      if (item.size === "small") {
        blockCount = 1
      } else if (item.size === "medium") {
        blockCount = 2
      } else {
        blockCount = 1
      }

      num += blockCount

      // å¦æå½åitemå¯ä»¥æ¾å¥å½åé¡µ
      if (blockCount <= remainingBlocks) {
        currentPage.push(item);
        remainingBlocks -= blockCount;
      }

      // å¦æå½åé¡µå·²æ»¡ï¼å°å½åé¡µæ°æ®æ·»å å°äºç»´æ°ç»ä¸­ï¼å¹¶éæ°åå§åå½åé¡µ
      if (remainingBlocks <= 0) {
        pageData.push(currentPage);
        currentPage = [];
        remainingBlocks = onePageCardNum;
      }
    }

    // å¤çæåä¸é¡µï¼å¦ææå©ä½åæ°ï¼æ·»å å°äºç»´æ°ç»ä¸­
    if (currentPage.length > 0) {
      pageData.push(currentPage);
    }

    let pageCount = Math.ceil(num / onePageCardNum)
    console.log("num", num)
    console.log("pageCount", pageCount)
    console.log("pageData", pageData)
    this.pageData = pageData
  }

  pageChange(index: number) {
    if (index < 0) {
      return
    }
    if (index > this.pageData.length - 1) {
      return
    }
    this.pageIndex = index
    console.log("pageIndex", this.pageIndex)
  }

  clickLeft() {
    const container = this.scrollContainerDiv.nativeElement
    let px = 256 * (this.data.playCardNum || 1)
    this.scrollLeft = container.scrollLeft - px
    container.scrollLeft -= px;
  }

  clickRight() {
    const container = this.scrollContainerDiv.nativeElement
    let px = 256 * (this.data.playCardNum || 1)
    this.scrollLeft = container.scrollLeft + px
    container.scrollLeft += px;
  }

  calculateGridTemplateColumns() {
    return this.data.columns ? `repeat(${this.data.columns}, 1fr)` : 'repeat(4, 1fr)';
  }

  checkLike(item) {
    item.isLike = !item.isLike
  }

  getTagBackgroundImage(tag) {
    let length = tag.length
    console.log("tag", length)
    if (length >= 4) {
      return 'url(assets/carousel-card/image/tag-bg-4.png)'
    } else if (length >= 3) {
      return 'url(assets/carousel-card/image/tag-bg-3.png)'
    } else {
      return 'url(assets/carousel-card/image/tag-bg-2.png)'
    }
  }

}
